import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { actFetchUserSignup } from "./duck/actions";
import { RootState } from "../../../store";
import { Link, Navigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
import "./../../HomeTemplate/_components/css/form.css";
import "./../../HomeTemplate/DetailMovie/style.css";

const alphaNumericRegex = /^[a-zA-Z0-9]+$/;
const phoneRegex = /^(0[3|5|7|8|9])+([0-9]{8})$/;
const vietnameseAlphaRegex =
  /^[a-zA-Z ]+$/;

const schema = yup.object({
  taiKhoan: yup
    .string()
    .required("(*) Vui lng nhp ti khon")
    .matches(alphaNumericRegex, "(*) Ti khon ch c nhp ch v s")
    .min(4, "(*) Ti khon phi c t nht 4 k t"),
  matKhau: yup
    .string()
    .required("(*) Vui lng nhp mt khu")
    .min(8, "(*) Mt khu phi c t nht 8 k t"),
  email: yup
    .string()
    .required("(*) Vui lng nhp email")
    .email("(*) Email khng ng nh dng"),
  soDt: yup
    .string()
    .required("(*) Vui lng nhp s in thoi")
    .matches(phoneRegex, "(*) S in thoi khng ng nh dng"),
  hoTen: yup
    .string()
    .required("(*) Vui lng nhp h tn")
    .matches(vietnameseAlphaRegex, "(*) H tn ch c nhp ch"),
});

export default function SignupPage() {
  const { data: userLogin } = useSelector(
    (state: RootState) => state.userReducer
  );
  const [isModalOpen, setIsModalOpen] = useState(false);
  const dispatch: any = useDispatch();
  const { loading, data: userSignup } = useSelector(
    (state: RootState) => state.userSignupReducer
  );

  const { handleSubmit, register, formState } = useForm<any>({
    defaultValues: {
      taiKhoan: "",
      matKhau: "",
      email: "",
      soDt: "",
      maNhom: "",
      hoTen: "",
    },
    mode: "onChange",
    resolver: yupResolver(schema),
  });

  const onSubmit = (formValues: any) => {
    dispatch(actFetchUserSignup(formValues));
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };

  if (userLogin) {
    if (userLogin.maLoaiNguoiDung === "QuanTri") {
      return <Navigate to={"/admin/dashboard"} />;
    }
    return <Navigate to={"/"} />;
  }

  return (
    <div className="container loginForm">
      <h4 className="loginTitle">ng k</h4>
      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="form-group">
          <label htmlFor="hoTen">H tn</label>
          <input
            type="text"
            className="form-control"
            id="hoTen"
            placeholder="Nguyen Van A"
            {...register("hoTen")}
          />
          <span className="text text-danger">
            {formState.errors.hoTen?.message as any}
          </span>
        </div>
        <div className="form-group">
          <label htmlFor="taiKhoan">Ti khon</label>
          <input
            type="text"
            className="form-control"
            id="taiKhoan"
            placeholder="hellocybermovie"
            {...register("taiKhoan")}
          />
          <span className="text text-danger">
            {formState.errors.taiKhoan?.message as any}
          </span>
        </div>
        <div className="form-group">
          <label htmlFor="matKhau">Mt khu</label>
          <input
            type="password"
            className="form-control"
            id="matKhau"
            placeholder="******"
            {...register("matKhau")}
          />
          <span className="text text-danger">
            {formState.errors.matKhau?.message as any}
          </span>
        </div>
        <div className="row">
          <div className="col-6">
            <div className="form-group">
              <label htmlFor="email">Email</label>
              <input
                type="text"
                className="form-control"
                id="email"
                placeholder="hellocybermovie@gmail.com"
                {...register("email")}
              />
              <span className="text text-danger">
                {formState.errors.email?.message as any}
              </span>
            </div>
          </div>
          <div className="col-6">
            <div className="form-group">
              <label htmlFor="soDt">S in thoi</label>
              <input
                type="text"
                className="form-control"
                id="soDt"
                placeholder="0901234567"
                {...register("soDt")}
              />
              <span className="text text-danger">
                {formState.errors.soDt?.message as any}
              </span>
            </div>
          </div>
        </div>

        <button disabled={loading} type="submit" className="btn btnLogin">
          ng k
        </button>
        <div>
          <p style={{ textAlign: "center" }}>
            Bn  c ti khon?
            <span>
              <Link to={"/auth/login"}>ng nhp</Link>
            </span>
          </p>
          <p style={{ textAlign: "center" }}>
            <Link to={"/"}>Quay v trang ch</Link>
          </p>
        </div>

        {loading && (
          <div
            style={{ textAlign: "center" }}
            className="spinner-border"
            role="status"
          >
            <span className="sr-only">Loading...</span>
          </div>
        )}
      </form>
      {isModalOpen && (
        <div className="modal">
          <div className="modal-dialog">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">
                  {userSignup
                    ? "ng k thnh cng"
                    : "ng k khng thnh cng"}
                </h5>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-label="Close"
                  onClick={closeModal}
                >
                  <Link to={"/"}>
                    <span aria-hidden="true"></span>
                  </Link>
                </button>
              </div>
              <div className="modal-body">
                <p>
                  {userSignup
                    ? "Bn  ng k thnh cng"
                    : " c li xy ra, vui lng th li"}
                </p>
              </div>
              <div className="modal-footer">
                <Link to={"/auth/login"} className="btn btnAuthLogin">
                  ng nhp
                </Link>
                <Link to={"/"} className="btn btnBackToHome">
                  V trang ch
                </Link>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
